import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;

export default {
	data() {
		return {
			imgRemote: imgRemote,
			showTop: false,
			kind: 0,
			navBar: navBar,
			pbId: '',
			list: [],
			activeList: [],
			minute: '',
			second: '',
			config: []

		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		selectStage(index) {
			this.kind = index;
			this.pbId=this.activeList[index].id;
			this.panicBuylist();
		},
		panicBuylist() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let {
				pbId
			} = this;
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign,
					pbId: pbId
				},
				obj
			);
			rs.getRequest('panicBuyList', params, res => {
				let {
					data
				} = res;
				if (data.code == 200) {
					this.config = data.data;
					this.activeList = data.data.activeList;

					this.list = data.data.goodsList;
				}
			});
		},
		// 显示键盘
		showKey() {
			this.$refs.popup.open();
		}
	},
	onShow() {
		this.panicBuylist();
	},
	onPageScroll(e){
		if(e.scrollTop==0){
				this.showTop=false;
		}else{
			this.showTop=true;
		}
	}
};
